body {
    background-color: rgb(158, 158, 125);
}

a {
    color: rgb(64, 113, 219);
    font-size: 15px;
    &:hover {
        text-decoration: none;
        background: #000;
        height: 50px;
        display: block;
    }
    width: 100px;
}

p {
    color: rgb(216, 76, 150);
    font-size: 20px;
    text-decoration: underline;
}
.container {
    position: relative;
    width: 400px;
    height: 400px;
    margin: 20px auto;
    color: cornflowerblue;
}
.box-1 {
    position: absolute;
    width: 200px;
    height: 200px;
    color: blueviolet;
    display: block;

}

